@import url(https://fonts.googleapis.com/css?family=Open+Sans);
* {
  font-family: 'Open Sans', sans-serif;
}
body {
  background-color: white;
}

/*fire fox can't render anchor tags inside PDFs: below is a work around*/

@-moz-document url-prefix() {
  a:link:after,
  a:visited:after {
    content: " (" attr(href) ")";
    font-size: 90%;
    color: blue;
  }
  a {
    color: black;
  }
}

a:visited {
  color: blue;
}

li {
  padding-top: 3px;
  padding-bottom: 3px;
}

h1 {
  margin-left: 5px;
  margin: 5px 0px 5px 20px;
}

input::-webkit-input-placeholder {
  font-size: 15px;
  font-style: italic;
  opacity: 0.6;
}

input::-ms-input-placeholder {
  font-size: 15px;
  font-style: italic;
  opacity: 0.6;
}

input::placeholder {
  font-size: 15px;
  font-style: italic;
  opacity: 0.6;
}

.document {
  display: grid;
  grid-gap: 1em;
  grid-template-areas: "header header" "left right";
  grid-template-columns: 2fr 1fr;
  grid-template-rows: -webkit-min-content -webkit-min-content -webkit-min-content;
  grid-template-rows: min-content min-content min-content;
  width: 800px;
  margin: auto;
  height: -webkit-min-content;
  height: -moz-min-content;
  height: min-content;
}

.grid-area-right-column {
  grid-area: right;
  background-color: white;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: -webkit-min-content -webkit-min-content -webkit-min-content -webkit-min-content;
  grid-template-rows: min-content min-content min-content min-content;
  grid-gap: 1em;
}

/* REMOVE CLASS*/

.remove {
  position: relative;
}

.remove button {
  position: absolute;
  top: 0;
  left: calc(100% - 84px);
  z-index: 1;
  height: 50px;
  background-color: rgb(0, 0, 226);
  color: #fff;
  font-weight: bold;
  font-size: 15px;
  border: none;
  transition: background-color 0.5s;
  outline: none;
}

.remove button:hover {
  background-color: blue;
  cursor: pointer;
}

.remove:hover button {
  display: block;
}

@media print {
  .remove {
    display: none !important;
  }
  a:link:after,
  a:visited:after {
    content: " (" attr(href) ")" !important;
    font-size: 90%;
    color: blue;
  }
